import { Component, Input, EventEmitter, Output } from '@angular/core';

@Component({
  selector: 'app-confirm-modal',
  templateUrl: './confirm-modal.component.html',
  styleUrls: ['./confirm-modal.component.scss']
})
export class ConfirmModalComponent {
  @Input() public confirmQuestion =
    'Are you sure you want to complete this action?';
  @Input() public confirmBtnMessage = 'Yes';
  @Input() public cancelBtnMessage = 'Cancel';
  @Output() public handleConfirmation = new EventEmitter<boolean>();

  constructor() {}

  public handleConfirmBtnClick() {
    this.handleConfirmation.emit(true);
  }

  public handleCancelBtnClick() {
    this.handleConfirmation.emit(false);
  }

  public handleModalClosed() {
    this.handleConfirmation.emit(false);
  }
}
