import {
  Component,
  OnInit,
  OnDestroy,
  Output,
  EventEmitter,
  Renderer2,
  Input
} from '@angular/core';
@Component({
  selector: 'app-modal',
  templateUrl: './modal.component.html',
  styleUrls: ['./modal.component.scss']
})
export class ModalComponent implements OnInit, OnDestroy {
  @Input() hideModalTitle = false;
  @Input() modalTitleText = '';
  @Output()
  public modalShouldClose = new EventEmitter<void>();
  private needsBackdrop = true;
  constructor(private renderer: Renderer2) {}
  public ngOnInit() {
    this.needsBackdrop = !document.documentElement.classList.contains(
      'no-scroll'
    );
    this.needsBackdrop &&
      this.renderer.addClass(document.documentElement, 'no-scroll');
  }
  public ngOnDestroy() {
    this.needsBackdrop &&
      this.renderer.removeClass(document.documentElement, 'no-scroll');
  }
}
