import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { IUserProfile } from './user';
import { environment } from 'src/environments/environment';
import { HttpClient } from '@angular/common/http';
import { AuthenticationService } from '../authentication/authentication.service';

@Injectable({
  providedIn: 'root'
})
export class UserService {
  private userListURL = `${environment.apiBaseUrl}/user/filter/tech-mngr/sites`;

  constructor(
    private httpClient: HttpClient,
    private authenticationService: AuthenticationService
  ) {}

  private get primarySiteId(): string {
    const userProfile = this.authenticationService.currentUserProfile$.getValue();
    if (userProfile && userProfile.primarySite) {
      return userProfile.primarySite.id;
    }
    return null;
  }

  public get primaryOrganisationId(): string {
    const userProfile = this.authenticationService.currentUserProfile$.getValue();
    if (userProfile && userProfile.primarySite) {
      return userProfile.primarySite.organisationId;
    }
    return null;
  }

  public getUserList(user: string): Observable<IUserProfile[]> {
    return this.httpClient.get<IUserProfile[]>(
      `${this.userListURL}/${this.primarySiteId}?OrganisationId=${this.primaryOrganisationId}&searchText=${user}`
    );
  }
}
