import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { IAssets } from 'src/app/services';

@Component({
  selector: 'app-pick-asset-item',
  templateUrl: './pick-asset-item.component.html',
  styleUrls: ['./pick-asset-item.component.scss']
})
export class PickAssetItemComponent implements OnInit {
  @Input() assetItem: IAssets;
  @Output() deleteAssetItem = new EventEmitter();

  constructor() {}

  ngOnInit(): void {}

  public handleDeleteAssetItem(asset: IAssets) {
    this.deleteAssetItem.emit(asset);
  }
}
