import {
  Component,
  ElementRef,
  EventEmitter,
  Input,
  OnInit,
  Output,
  QueryList,
  ViewChild,
  ViewChildren
} from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { Subscription } from 'rxjs/internal/Subscription';
import { debounceTime, filter } from 'rxjs/operators';
import {
  CheckListsService,
  ICheckList,
  ITask,
  ToastService
} from 'src/app/services';

@Component({
  selector: 'app-pick-check-lists',
  templateUrl: './pick-check-lists.component.html',
  styleUrls: ['./pick-check-lists.component.scss']
})
export class PickCheckListsComponent implements OnInit {
  public isLoading: Boolean = false;
  public isSearching = false;
  public searchForm: FormGroup;
  public searchCheckListsSubscription: Subscription;
  public searchValueSubscription: Subscription;
  public searchResults: ICheckList[] = [];
  public searchCheckListTaskResults: ITask[] = [];
  public isCheckListsSelected = false;
  public searchResultsVisible = false;
  @ViewChildren('searchResult')
  public searchResultElements: QueryList<ElementRef>;
  @ViewChild('searchInput')
  public searchInputElement: ElementRef;
  public listCheckLists: ICheckList[] = [];
  public itemCount: number;
  @Input() existingChecklists: ICheckList[] = [];
  @Output() addedCheckLists = new EventEmitter();
  public expanded: boolean = false;

  constructor(
    private formBuilder: FormBuilder,
    private checkListsService: CheckListsService,
    private toastService: ToastService
  ) {
    this.searchForm = this.formBuilder.group({
      text: '',
      checkLists: null
    });
  }

  public ngOnInit(): void {
    this.searchForm
      .get('text')
      .valueChanges.pipe(
        debounceTime(700),
        filter(textValue => textValue.length >= 2),
        filter(textValue => !this.checkListsNameIsInSearchResults(textValue))
      )
      .subscribe(() => {
        this.searchCheckLists();
      });

    this.searchForm
      .get('text')
      .valueChanges.pipe(
        debounceTime(700),
        filter(textValue => textValue.length < 2)
      )
      .subscribe(() => (this.searchResultsVisible = false));
    this.listCheckLists.push(...this.existingChecklists);
  }

  public checkListsNameIsInSearchResults(searchText: string) {
    let result = false;
    for (const checkList of this.searchResults) {
      if (checkList.number + ' - ' + checkList.name === searchText) {
        result = true;
      }
    }
    return result;
  }

  public searchCheckLists() {
    this.searchCheckListsSubscription &&
      !this.searchCheckListsSubscription.closed &&
      this.searchCheckListsSubscription.unsubscribe();
    const textValue = this.searchForm.get('text').value;
    if (this.isCheckListsSelected === false) {
      this.isSearching = true;
      this.searchResultsVisible = true;
      this.searchCheckListsSubscription = this.checkListsService
        .getCheckListsList({
          offset: 0,
          take: 100,
          searchText: textValue,
          includeObsolete: false
        })
        .subscribe((data: ICheckList[]) => {
          this.searchResults = [];
          data.forEach(x => {
            const newCheckList: ICheckList = {
              id: x.id,
              number: x.number,
              name: x.name,
              numberWithPrefix: x.numberWithPrefix
            };
            this.searchResults.push(newCheckList);
          });
          this.isSearching = false;
        });
    }
    this.isLoading = false;
  }

  public clearSearchForm() {
    this.searchForm.setValue({ text: '', checkLists: null });
    this.searchResultsVisible = false;
  }

  public handleSearchBlur() {
    try{
      setTimeout(() => {
        try{
        const searchValue = this.searchForm.get('text').value;
        const activeElement: HTMLElement = document.activeElement as HTMLElement;
        if (
          this.checklistsTaskIsInSearchResults(searchValue) ||
          activeElement.dataset.checklisttaskname
        ) {
          return true;
        }
        this.clearSearchForm();
      }
      catch(e) { console.log(e)}
      }, 0);
    }

 catch(e)
 {
  console.log(e)
 }
  }

  public checklistsTaskIsInSearchResults(searchText: string) {
    let result = false;
    for (const checklists of this.searchResults) {
      if (
        checklists.numberWithPrefix + ' - ' + checklists.name ===
        searchText
      ) {
        result = true;
      }
    }
    return result;
  }

  public handleSearchKeyUp(event) {
    this.isCheckListsSelected = false;
    switch (event.key) {
      case 'ArrowDown':
        const resultButtons = this.searchResultElements.toArray();
        resultButtons.length > 0 && resultButtons[0].nativeElement.focus();
        break;
      case 'Escape':
        this.clearSearchForm();
        break;
    }
  }

  public handleSearchResultClick(checklist) {
    console.log(checklist);
    setTimeout(() => {
      if (this.isCheckListsSelected === false) {
        this.isSearching = true;
        this.searchResultsVisible = true;
        const searchValue = checklist.number;
        this.searchValueSubscription &&
          !this.searchValueSubscription.closed &&
          this.searchValueSubscription.unsubscribe();
        this.searchValueSubscription = this.checkListsService
          .viewCheckListForId(searchValue)
          .subscribe((checkListDetail: ICheckList) => {
            if (
              this.listCheckLists.filter(
                addCheckList => addCheckList.id === checkListDetail.id
              ).length !== 0
            ) {
              let checkListId = this.listCheckLists.filter(
                addcheckList => addcheckList.id === checklist.id
              );
              this.toastService.showErrorToast(
                `${checkListId[0].numberWithPrefix} Checklist already selected`
              );
              return;
            }
            this.listCheckLists.push(checkListDetail);
            this.isSearching = false;
          });
        this.isLoading = false;
      }
      this.clearSearchForm();
    }, 0);
  }

  public handleSearchResultKeyUp(event) {
    switch (event.key) {
      case 'ArrowUp':
        const firstResultButton = this.searchResultElements.toArray()[0]
          .nativeElement;
        if (event.target === firstResultButton) {
          this.searchInputElement.nativeElement.focus();
        } else {
          event.target.previousSibling.focus();
        }
        break;
      case 'ArrowDown':
        event.target.nextSibling && event.target.nextSibling.focus();
        break;
      case 'Escape':
        this.clearSearchForm();
        break;
    }
  }

  public addCheckLists() {
    this.addedCheckLists.emit(this.listCheckLists);
  }

  public handleShowCheckListContent(checkList) {
    if (this.expanded === checkList) {
      this.expanded = null;
    } else {
      this.expanded = checkList;
    }
  }

  public handleDeleteCheckListItem(checkList: ICheckList) {
    this.listCheckLists.splice(this.listCheckLists.indexOf(checkList), 1);
  }
}
