import {
  Component,
  ElementRef,
  EventEmitter,
  Input,
  OnInit,
  Output,
  QueryList,
  ViewChild,
  ViewChildren
} from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { Subscription } from 'rxjs/internal/Subscription';
import { debounceTime, filter } from 'rxjs/operators';
import { Asset, AssetService, IAssets, ToastService } from 'src/app/services';

@Component({
  selector: 'app-pick-assets',
  templateUrl: './pick-assets.component.html',
  styleUrls: ['./pick-assets.component.scss']
})
export class PickAssetsComponent implements OnInit {
  public isLoading: Boolean = false;
  public isSearching = false;
  public searchForm: FormGroup;
  public searchAssetsSubscription: Subscription;
  public searchResults: IAssets[] = [];
  public isAssetsSelected = false;
  public searchResultsVisible = false;
  @ViewChildren('searchResult')
  public searchResultElements: QueryList<ElementRef>;
  @ViewChild('searchInput')
  public searchInputElement: ElementRef;
  public listAssets: IAssets[] = [];
  public itemCount: number;
  @Output() addedAssets = new EventEmitter();
  @Input() existingAssets: IAssets[];

  constructor(
    private formBuilder: FormBuilder,
    private assetService: AssetService,
    private toastService: ToastService
  ) {
    this.searchForm = this.formBuilder.group({
      text: '',
      assets: null
    });
  }

  public ngOnInit(): void {
    this.searchForm
      .get('text')
      .valueChanges.pipe(
        debounceTime(700),
        filter(textValue => textValue.length >= 3),
        filter(textValue => !this.assetsNameIsInSearchResults(textValue))
      )
      .subscribe(() => {
        this.searchAssets();
      });

    this.searchForm
      .get('text')
      .valueChanges.pipe(
        debounceTime(700),
        filter(textValue => textValue.length < 3)
      )
      .subscribe(() => (this.searchResultsVisible = false));
    this.listAssets.push(...this.existingAssets);
    this.countAssetItems();
  }

  public assetsNameIsInSearchResults(searchText: string) {
    let result = false;
    for (const assets of this.searchResults) {
      if (assets.assetId + ' - ' + assets.name === searchText) {
        result = true;
      }
    }
    return result;
  }

  public searchAssets() {
    this.searchAssetsSubscription &&
      !this.searchAssetsSubscription.closed &&
      this.searchAssetsSubscription.unsubscribe();
    const textValue = this.searchForm.get('text').value;
    if (this.isAssetsSelected == false) {
      this.isSearching = true;
      this.searchResultsVisible = true;
      this.searchAssetsSubscription = this.assetService
        .listAssets({
          offset: 0,
          take: 100,
          text: textValue
        })
        .subscribe((data: Asset[]) => {
          this.searchResults = [];
          data.forEach(x => {
            const newAsset: IAssets = {
              referenceId: null,
              id: x.id,
              location: x.location,
              assetId: x.assetId,
              assetCode: x.assetCode,
              name: x.name,
              description: x.description,
              isDelete: false,
              isNew: true
            };
            this.searchResults.push(newAsset);
          });

          this.isSearching = false;
        });
    }
    this.isLoading = false;
  }

  public clearSearchForm() {
    this.searchForm.setValue({ text: '', assets: null });
    this.searchResultsVisible = false;
  }

  public handleSearchKeyUp(event) {
    debugger
    this.isAssetsSelected = false;
    switch (event.key) {
      case 'ArrowDown':
        const resultButtons = this.searchResultElements.toArray();
        resultButtons.length > 0 && resultButtons[0].nativeElement.focus();
        break;
      case 'Escape':
        this.clearSearchForm();
        break;
    }
  }

  public handleSearchBlur() {
    debugger
    setTimeout(() => {
      const searchValue = this.searchForm.get('text').value;
      const activeElement: HTMLElement = document.activeElement as HTMLElement;
      if (
        this.assetsNameIsInSearchResults(searchValue) ||
        activeElement.dataset.assetname
      ) {
        return;
      }
      this.clearSearchForm();
    }, 0);
  }

  public handleSearchResultKeyUp(event) {
    debugger
    switch (event.key) {
      case 'ArrowUp':
        const firstResultButton = this.searchResultElements.toArray()[0]
          .nativeElement;
        if (event.target === firstResultButton) {
          this.searchInputElement.nativeElement.focus();
        } else {
          event.target.previousSibling.focus();
        }
        break;
      case 'ArrowDown':
        event.target.nextSibling && event.target.nextSibling.focus();
        break;
      case 'Escape':
        this.clearSearchForm();
        break;
    }
  }

  public handleSearchResultClick(asset: IAssets) {
    this.searchForm.setValue({
      text: asset.assetId + ' - ' + asset.name,
      assets: asset.assetId
    });

    if (
      this.listAssets.filter(
        addAsset => asset.assetId === addAsset.assetId && addAsset.isDelete
      ).length !== 0
    ) {
      this.listAssets.find(
        addAsset => asset.assetId === addAsset.assetId && addAsset.isDelete
      ).isDelete = false;
      this.clearSearchForm();
      this.searchResultsVisible = false;
      return;
    }

    if (
      this.listAssets.filter(
        addAsset => asset.assetId === addAsset.assetId && !addAsset.isDelete
      ).length !== 0
    ) {
      this.toastService.showErrorToast(
        `${asset.assetId} Asset already selected`
      );
      this.clearSearchForm();
      this.searchResultsVisible = false;
      return;
    }

    this.addAssetsList(asset);
    this.searchResultsVisible = false;
    this.isAssetsSelected = true;
  }

  public addAssetsList(asset: IAssets) {
    asset.isNew = true;
    this.listAssets.push(asset);
    this.countAssetItems();
    this.searchForm.setValue({
      text: '',
      assets: null
    });
  }

  public deleteAssetItem(asset: IAssets) {
    if (asset.isNew) {
      this.listAssets.splice(this.listAssets.indexOf(asset), 1);
    } else {
      this.listAssets.find(x => x.id === asset.id).isDelete = true;
    }
    this.countAssetItems();
  }

  public addAssets() {
    this.addedAssets.emit(this.listAssets);
  }

  public countAssetItems() {
    this.itemCount = this.listAssets.filter(
      asset => asset.isDelete !== true
    ).length;
  }
}
