import { Component, Input, OnInit } from '@angular/core';
import { ITask } from 'src/app/services';

@Component({
  selector: 'app-pick-task-item',
  templateUrl: './pick-task-item.component.html',
  styleUrls: ['./pick-task-item.component.scss']
})
export class PickTaskItemComponent implements OnInit {
  @Input() public taskItem: ITask;
  @Input() public firstItem: boolean;
  public textMaxLength = 90;
  public isViewTaskItemDetail: boolean = false;

  constructor() {}

  ngOnInit(): void {}

  public handleViewMoreClick() {
    this.isViewTaskItemDetail = !this.isViewTaskItemDetail;
  }
}
