import {
  Component,
  OnDestroy,
  OnInit,
  ViewChildren,
  ElementRef,
  ViewChild,
  QueryList,
  Input,
  Output,
  EventEmitter
} from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { Subscription } from 'rxjs';
import { debounceTime, filter } from 'rxjs/operators';
import { IUserProfile } from 'src/app/services';
import { UserService } from 'src/app/services/user-account/user.service';

@Component({
  selector: 'app-list-users',
  templateUrl: './list-users.component.html',
  styleUrls: ['./list-users.component.scss']
})
export class ListUsersComponent implements OnInit, OnDestroy {
  public listUsersResultsVisible = false;
  public isUsersSearching = false;
  public listUsersResults: IUserProfile[] = [];
  public listUsersForm: FormGroup;
  public listUsersSubscription: Subscription;
  @ViewChildren('searchResult')
  public searchResultElements: QueryList<ElementRef>;
  @ViewChild('searchInput')
  public searchInputElement: ElementRef;
  public isUsersSelected = false;
  @Input() public userName: string;
  @Input() public isEditable: boolean = true;
  @Output() selectedUserId = new EventEmitter<string>();

  constructor(
    private formBuilder: FormBuilder,
    private userService: UserService
  ) {
    this.listUsersForm = this.formBuilder.group({
      text: '',
      userId: this.userName
    });
  }

  public ngOnInit(): void {
    this.listUsersForm
      .get('text')
      .valueChanges.pipe(
        debounceTime(700),

        filter(textValue => textValue.length >= 3),
        filter(textValue => !this.userNameIsInListResults(textValue))
      )
      .subscribe(() => this.listUsers());

    this.listUsersForm
      .get('text')
      .valueChanges.pipe(
        debounceTime(700),
        filter(textValue => textValue.length < 3)
      )
      .subscribe(() => {
        this.listUsersResultsVisible = false;
      });

    this.listUsersForm
      .get('text')
      .valueChanges.pipe(
        debounceTime(700),
        filter(textValue => textValue.length === 0)
      )
      .subscribe(() => {
        this.selectedUserId.emit('');
      });

    if (this.userName) {
      this.listUsersForm.setValue({ text: this.userName, userId: '' });
    }
  }

  public listUsers() {
    this.listUsersSubscription &&
      !this.listUsersSubscription.closed &&
      this.listUsersSubscription.unsubscribe();
    const textValue = this.listUsersForm.get('text').value;
    if (this.isUsersSelected === false) {
      this.isUsersSearching = true;
      this.listUsersResultsVisible = this.listUsersForm.get('text').dirty
        ? true
        : false;
      this.listUsersSubscription = this.userService
        .getUserList(textValue)
        .subscribe((data: IUserProfile[]) => {
          this.listUsersResults = data;
          this.isUsersSearching = false;
        });
    }
  }

  public handleUsersListKeyUp(event) {
    this.isUsersSelected = false;
    switch (event.key) {
      case 'ArrowDown':
        const resultButtons = this.searchResultElements.toArray();
        resultButtons.length > 0 && resultButtons[0].nativeElement.focus();
        break;
      case 'Escape':
        this.clearlistUsersForm();
        break;
    }
  }

  public handleUsersListResultKeyUp(event) {
    switch (event.key) {
      case 'ArrowUp':
        const firstResultButton = this.searchResultElements.toArray()[0]
          .nativeElement;
        if (event.target === firstResultButton) {
          this.searchInputElement.nativeElement.focus();
        } else {
          event.target.previousSibling.focus();
        }
        break;
      case 'ArrowDown':
        if (event.target.nextElementSibling != null) {
          event.target.nextSibling && event.target.nextSibling.focus();
        }
        break;
      case 'Escape':
        this.clearlistUsersForm();
        break;
    }
  }

  public handleUsersListResultClick(user: IUserProfile) {
    const selectedUser: string = user.userId
      .concat('-', user.displayName)
      .substring(0, 50);

    this.listUsersForm.setValue({
      text: selectedUser,
      userId: selectedUser
    });
    this.listUsersResultsVisible = false;
    this.isUsersSelected = true;
    this.selectedUserId.emit(selectedUser);
  }

  public handleUsersListBlur() {
    if (this.isEditable) {
      setTimeout(() => {
        const searchValue = this.listUsersForm.get('text').value;
        const activeElement: HTMLElement = document.activeElement as HTMLElement;
        if (
          this.userNameIsInListResults(searchValue) ||
          activeElement.dataset.sitename
        ) {
          return;
        }
        if (!searchValue) {
          this.clearlistUsersForm();
        }
      }, 0);
    }
  }

  public userNameIsInListResults(searchText: string) {
    let result = false;
    for (const user of this.listUsersResults) {
      let selectedUser: string = user.userId
        .concat('-', user.displayName)
        .substring(0, 50);
      if (selectedUser === searchText) {
        result = true;
      }
    }
    return result;
  }

  public clearlistUsersForm() {
    this.listUsersForm.setValue({ text: '', userId: null });
    this.listUsersResultsVisible = false;
  }

  public ngOnDestroy() {
    this.listUsersSubscription &&
      !this.listUsersSubscription.closed &&
      this.listUsersSubscription.unsubscribe();
  }
}
